const animation = {
    initial: {
        scale: 0,
        opacity: 0
    },
    animate: {
        opacity: 1,
        scale: 1
    }
}

const goToTwitter = () => {
    window.location = 'https://twitter.com/EvmosDomains/status/1530626436574457856';
}

const openBlogPost = () => {
    window.open('https://blog.evmos.domains/the-evmos-domains-mainnet-launch-596ac1a50936')
}

export default ({ match }) => {
    const [now, setNow] = useState(moment());

    const formatCountdownNumbers = (time) => {
        const numbers = time.map(t => {
            if (String(Math.floor(t)).length == 1)
            return `0${t}`;
            return `${t}`;
        })
        const [days, hours, minutes, seconds] = numbers;
        return `${days} Days - ${hours} hours - ${minutes} minutes - ${seconds} seconds`;
    }

    const countdown = () => {
        const duration = moment.duration(moment('2022-06-07T00:00:00-00:00').diff(now));
        const { days, hours, minutes, seconds } = duration._data;
        return formatCountdownNumbers([days, hours, minutes, seconds]);
    }

    const runUpdater = () => {
        setTimeout(() => {
            setNow(moment());
            console.log("now set to", moment().format())
        }, 1000)
    }

    runUpdater();

    return (
    <WrapperWrapper>
        <Wrapper
            initial={animation.initial}
            animate={animation.animate}>
            <BackgroundWrapper>
                <Background onClick={goToTwitter}>
                    <LaunchTitle>
                        <LaunchLogo src="/logo.svg" />
                        <LaunchText>Mainnet <LaunchWord>launch</LaunchWord> in:</LaunchText>
                    </LaunchTitle>
                    <Countdown>{(countdown())}</Countdown>
                </Background>
                <StartText>**Mainnet registrations begin here on 06/07/2022 at 00:00:00 UTC**</StartText>
                <BlogPostText>More details about pre-registration and names can be found in our official <BlogPostLink onClick={openBlogPost}>Launch Post</BlogPostLink></BlogPostText>
            </BackgroundWrapper>
        </Wrapper>
        </WrapperWrapper>
    )
}
